<template>
  <div class="trialpreson flex-v page conclusionsedit"
    style="position:relative;">

    <div class="headertitle flex">
      <el-breadcrumb separator-class="el-icon-arrow-right"
        class="my-breadcrumb ">
        <el-breadcrumb-item :to="{ name: 'TotalCheck' }">总检台</el-breadcrumb-item>
        <el-breadcrumb-item>检查结论</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="flex rightbtn">
        <div class="marginright50"
          @click="detailedTemplate"><span><img src="@/assets/img/xmmxIcon.png" /></span>项目明细
        </div>
        <div class="marginright50"
          @click="openTemplate"><span><img src="@/assets/img/dqmbIcon.png" /></span>调取模板
        </div>
        <div class="marginright50"
          v-if="!checkjlshow"
          @click="reviewremind"><span><img src="@/assets/img/fctxIcon.png" /></span>复查提醒</div>
        <div v-if="!checkjlshow"
          @click="reviewrReport"
          class="marginright50">
          <span style="margin-right:5px">
            <img src="@/assets/img/ylbgIcon.png" />
          </span>预览报告
        </div>
      </div>
    </div>
    <div class="tabledata">
      <div class="rightconcent flex-v">
        <div class="toppresonxinxi"
          style="border:none">
          <div class="presonIcon flex-v-center">
            <img v-if="ponserData.addOn1"
              :src="ponserData.addOn1"
              alt=''>
            <template v-else>
              <img v-if="ponserData.sexType==2"
                src="@/assets/img/girlbackImg.png" />
              <img v-else
                src="@/assets/img/boxbackImg.png" />
            </template>
          </div>
          <div class="presoncontent flex-item">
            <div class="cell-item-list">
              <div class="cell-item">
                <label class="lable">姓名 : </label>
                <template class="template">{{ponserData.userName}}</template>
              </div>
              <div class="cell-item">
                <label class="lable">性别 : </label>
                {{
                  ponserData.sexType == 1 ? '男' :
                  ponserData.sexType == 2 ? '女' : '不详'
                }}
              </div>
              <div class="cell-item">
                <label class="lable">年龄 : </label>
                <template class="template">{{ponserData.age}}</template>
              </div>
              <div class="cell-item">
                <label class="lable">居民类型 : </label>
                <template class="template">{{ponserData.personType | getBoxType}}</template>
              </div>
              <div class="cell-item">
                <label class="lable"
                  style="min-width:62px">既往史 : </label>
                <template class="template">
                  {{historyDisease | emptyData}}
                </template>
              </div>
              <div class="cell-item">
                <label class="lable">总审医生 : </label>
                <template class="template ">{{ponserData.twoAuthUserName}}</template>
              </div>
              <div class="cell-item">
                <label class="lable">总审日期 : </label>
                <template class="template ">{{ponserData.updateDate | formatDate}}</template>
              </div>
            </div>
          </div>

        </div>
        <div class="flex-v flex-item">
          <div class="tabbtn">
            <div :class="{active:!showIndex}">
              <span><i class="icon iconfont bj-jcjl-icon"></i></span>
              检查结论
            </div>
            <div :class="{active:showIndex}">
              <span>
                <i class="icon iconfont bj-wxts-icon"
                  style="font-size: 20px;"></i>
              </span>
              温馨提示
            </div>
          </div>
          <div class="jlmain flex-item"
            style="overflow:auto">
            <div class="jlcentent"
              v-if="checkjlshow">
              <div class="jl-list "
                v-for="item in projectList"
                :key="item.sysGuid">
                <div class="big-title">{{item.pname}}</div>
                <div v-for="items in item.smallProject"
                  :key='items.projectId'
                  style="padding-left:1em">
                  <div class="flex">
                    <div style="line-height:31px">{{items.pname}}：</div>
                    <el-input type="textarea"
                      class="flex-item"
                      autosize
                      resize='none'
                      v-model="items.checkConclusion">
                    </el-input>
                  </div>
                  <div class="flex">
                    <div style="width:auto;line-height: 31px;margin-right:5px">诊断意见或印象:</div>
                    <el-input type="textarea"
                      resize='none'
                      autosize
                      v-model="items.checkProposal"
                      style="flex:1">
                    </el-input>
                  </div>
                </div>
              </div>
            </div>
            <div class="wxtxcentent jlcentent"
              v-if="!checkjlshow">
              <div class="jl-list"
                v-for="item in projectList"
                :key="item.sysGuid">
                <div class="big-title">{{item.pname}}</div>
                <div v-for="items in item.smallProject"
                  :key="items.projectId"
                  style="padding-left:1em">
                  <div class="flex">
                    <div style="line-height: 31px">{{items.pname}}：</div>
                    <el-input type="textarea"
                      autosize
                      resize='none'
                      v-model="items.checkWaring"
                      class="flex-item">
                    </el-input>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="footer noboxshow">
      <template v-if="checkjlshow">
        <el-button size="small"
          class="btn-hollowing"
          @click="cancel">取消</el-button>
        <el-button size="small"
          class="btn-info"
          @click="nextStep">下一步</el-button>
      </template>
      <template v-else>
        <el-button size="small"
          class="btn-hollowing"
          @click="prevStep">上一步</el-button>
        <el-button size="small"
          class="btn-info"
          @click="saveResult"
          :loading="saveLoading">保存</el-button>
        <el-button size="small"
          class="btn-red"
          @click="rejectedevent">驳回</el-button>
      </template>
    </div>

    <!-- 驳回弹窗 -->
    <div class="add_dialog_wraper">
      <el-dialog title="提示"
        :visible.sync="centerDialogrejected"
        width="505px"
        :lock-scroll="false"
        :close-on-click-modal="false"
        center>
        <div class="bohuiyy">驳回原因：</div>
        <div class="bohuiyymain">
          <el-input type="textarea"
            :rows="5"
            resize='none'
            placeholder="请输入"
            v-model="backReason"
            maxlength="100"
            show-word-limit />
        </div>
        <span slot="footer"
          class="dialog-footer">
          <el-button class="btn-hollowing"
            @click="centerDialogrejected=false">取消</el-button>
          <el-button class="btn-info"
            @click="yescenterDialogrejected">确认</el-button>
        </span>
      </el-dialog>
    </div>

    <!-- 复查提醒弹窗 -->
    <div class="add_dialog_wraper reviewshow">
      <el-dialog title="复查提醒"
        :visible.sync="centerDialogreview"
        width="600px"
        :lock-scroll="false"
        :close-on-click-modal="false"
        center>
        <el-form ref="reviewForm"
          :model="reviewForm"
          size="mini">
          <el-form-item label="复查日期：">
            <el-date-picker type="date"
              placeholder="选择日期"
              v-model="reviewForm.recheckTime"
              style="width: 150px;"
              :picker-options="{disabledDate: time =>time<(Date.now()-86400000)}">
            </el-date-picker>
            <!-- value-format="yyyy-MM-dd" -->
            <!-- 取未来值吗?  -->
          </el-form-item>
          <el-form-item>
            <div>复查内容：</div>
            <el-input type="textarea"
              :autosize="{ minRows: 4, maxRows: 6}"
              resize='none'
              v-model="reviewForm.recheckContent"
              maxlength="100"
              show-word-limit />
          </el-form-item>
          <div style="overflow: hidden;height: auto;">
            <el-col :span="12">
              <el-form-item label="记录日期：">
                <template>{{systime}}</template>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="记录人员："
                label-width="100px">
                <template v-if="o_userinfo.realName">{{o_userinfo.realName}}</template>
                <template v-else>-</template>
              </el-form-item>
            </el-col>
          </div>
        </el-form>
        <span slot="footer"
          class="dialog-footer">
          <el-button class="btn-hollowing"
            @click="centerDialogreview=false">取消</el-button>
          <el-button class="btn-info"
            @click="yescenterDialogreview"
            :loading="reviewLoading">保存</el-button>
        </span>
      </el-dialog>
    </div>

    <!-- 调取模板 -->
    <div class="add_dialog_wraper templateshow">
      <el-dialog title="调取模板"
        class="flex"
        :visible.sync="centerDialogtemplate"
        width="960px"
        :lock-scroll="false"
        :close-on-click-modal="false"
        center>
        <div style="height:400px;overflow:hidden;display:flex">
          <div class="leftmeun">
            <el-menu :default-active="defaultActive"
              class="el-menu-vertical-demo">
              <el-submenu :index="item.sysGuid"
                v-for="item in templateData"
                :key="item.sysGuid">
                <template slot="title">
                  <i class="iconfont bj-keshiIcon"></i>
                  <span>{{item.dname}}</span>
                </template>
                <el-menu-item :index="items.projectId"
                  v-for="items in item.bigList"
                  :key="items.projectId"
                  @click.native="changeSide(items)">
                  <div class="flex-v-center menu-title">{{items.pname}}</div>
                </el-menu-item>
              </el-submenu>
            </el-menu>
          </div>
          <div class="rightoncent flex-item">
            <el-table :data="templateTableData"
              class="z-el-table"
              ref="multipleTable"
              height="100%"
              @row-click='handleRowClick'
              :row-class-name='handleRowClass'>
              <el-table-column width="36">
                <span class="dot"></span>
              </el-table-column>
              <el-table-column prop="projectName"
                label="小项名称"
                width="120"
                show-overflow-tooltip />
              <template v-if='!showIndex'>
                <el-table-column prop="checkConclusion"
                  label="检查结论"
                  :formatter='row => $options.filters.emptyData(row.checkConclusion)'
                  show-overflow-tooltip>
                </el-table-column>
                <el-table-column prop="checkProposal"
                  label="医生建议"
                  :formatter='row => $options.filters.emptyData(row.checkProposal)'
                  show-overflow-tooltip>
                </el-table-column>
              </template>
              <template v-else>
                <el-table-column prop="checkWaring"
                  label="温馨提示"
                  show-overflow-tooltip>
                </el-table-column>
              </template>
            </el-table>
          </div>
        </div>

        <span slot="footer"
          class="dialog-footer">
          <el-button class="btn-hollowing"
            @click="centerDialogtemplate=false">取消</el-button>
          <el-button class="btn-info"
            @click="viewTemplate(showIndex?['checkWaring']:['checkConclusion', 'checkProposal'])">保存</el-button>
        </span>
      </el-dialog>
    </div>

    <el-dialog :visible.sync='showPreview'
      class="flex"
      title="体检报告"
      width="1120px"
      center>
      <preview-report :examUserId='examUserId'
        ref="report" />
      <div slot="footer">
        <el-button class='btn-hollowing'
          @click="showPreview=false">取消</el-button>
        <el-button class='btn-info'
          @click="$refs.report.printPage()">打印</el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import { formatDate, calcType } from '@/lib/utils'

import PreviewReport from './previewReport'
export default {
  name: 'conclusionsedit',
  components: { PreviewReport },
  data() {
    return {
      systime: '',
      showIndex: 0, //检查结论tab
      checkjlshow: true, //检查结论模块
      backReason: '', //驳回原因
      centerDialogrejected: false,
      centerDialogreview: false,
      centerDialogtemplate: false,
      ponserData: '', //人员信息
      projectList: [],
      reviewForm: {
        sysGuid: '',
        recheckTime: '',
        recheckContent: '',
        recordTime: '',
        recordMan: ''
      },

      templateData: [],
      defaultActive: '',

      templateTableData: [],
      jlperson: '',
      multipleSelection: [],
      selectionRules: [],
      showPreview: false, // 显示预览报告
      reviewLoading: false, // 保存复查提醒按钮loading
      saveLoading: false
    }
  },

  created() {
    this.examUserId = this.$route.query.examUserId
    this.initData()
  },

  methods: {
    // 页面初始数据
    initData() {
      this.getData(this.examUserId)
      this.getTemplate(this.examUserId)
    },

    // 给选中的模板数据加类名样式
    handleRowClass({ row }) {
      let isSelection = this.selectionRules.some(item => item.sysGuid === row.sysGuid)
        ? 'is-selection'
        : ''
      return `${isSelection} hand-shape`
    },

    // 点击模板侧边导航
    changeSide({ resultList, projectId }) {
      // console.log(item)
      this.defaultActive = projectId // 手动更新菜单高亮（不更新永远是初始值）
      this.templateTableData = resultList.filter(item =>
        this.showIndex ? item.checkWaring : item.checkConclusion || item.checkProposal
      )
    },

    // 选取模板中的数据
    handleRowClick(rule) {
      let i = this.selectionRules.findIndex(item => item.projectId === rule.projectId)
      if (~i) {
        if (this.selectionRules.some(t => t.sysGuid === rule.sysGuid)) {
          this.selectionRules.splice(i, 1)
        } else {
          this.selectionRules.splice(i, 1, rule)
        }
      } else {
        this.selectionRules.push(rule)
      }
    },

    // 把选中的模板数据渲染出来
    viewTemplate(keys) {
      this.centerDialogtemplate = false
      if (!this.selectionRules.length) return

      this.projectList = this.projectList.map(item => {
        item.smallProject = item.smallProject.map(items => {
          let rule = this.selectionRules.find(t => items.projectId === t.projectId)
          if (rule) {
            keys.forEach(key => {
              items[key] = rule[key]
            })
          }
          return items
        })
        return item
      })

      // 清空选中的规则
      this.$nextTick(() => (this.selectionRules = []))
    },

    // 取消返回
    cancel() {
      this.$router.push({
        name: 'TotalCheck'
      })
    },

    // 上一步
    prevStep() {
      this.selectionRules = []
      this.showIndex = 0
      this.checkjlshow = true
    },

    //下一步
    nextStep() {
      this.selectionRules = []
      this.showIndex = 1
      this.checkjlshow = false
    },

    //保存
    saveResult() {
      let commitData = []
      this.projectList.forEach(item => {
        commitData = commitData.concat(
          item.smallProject.map(items => {
            items.flow = '2'
            return items
          })
        )
      })
      let url = '/dev/examUserProjectItem/addAndCheck'
      this.saveLoading = true
      this.$post(url, commitData, 'json')
        .then(res => {
          this.saveLoading = false
          this.$router.push({
            name: 'conclusions',
            query: {
              sysGuid: this.examUserId
            }
          })
        })
        .catch(error => {
          this.saveLoading = false
          this.$message.error(error)
        })
    },

    // 打开驳回弹框
    rejectedevent() {
      this.centerDialogrejected = true
    },

    // 调取驳回
    yescenterDialogrejected() {
      let url = '/dev/examUserProjectItem/backReason'
      const params = {
        sysGuid: this.examUserId,
        backReason: this.backReason
      }
      this.$post(url, params)
        .then(res => {
          this.centerDialogrejected = false
          this.$message.success('驳回成功')
          this.$router.replace({
            name: 'TotalCheck'
          })
        })
        .catch(error => {
          this.$message.error(error)
        })
    },

    // 复查提醒
    reviewremind() {
      this.centerDialogreview = true
      this.systime = formatDate(new Date())
    },

    // 预览报告
    reviewrReport() {
      this.showPreview = true
    },

    // 提交复查
    yescenterDialogreview() {
      let url = '/user/examUser/reviewRemind'
      const params = {
        sysGuid: this.examUserId,
        recheckTime: formatDate(this.reviewForm.recheckTime),
        recheckContent: this.reviewForm.recheckContent,
        recordTime: this.systime,
        recordMan: this.o_userinfo.realName
      }
      this.reviewLoading = true
      this.$post(url, params)
        .then(res => {
          this.reviewLoading = false
          this.centerDialogreview = false
          this.$message.success('复查提醒创建成功')
        })
        .catch(error => {
          this.reviewLoading = false
          this.$message.error(error)
        })
    },

    // 调取模板(打开)
    openTemplate() {
      // 过滤为空的数据
      this.templateTableData = this.templateData[0].bigList[0].resultList.filter(item =>
        this.showIndex ? item.checkWaring : item.checkConclusion || item.checkProposal
      )
      this.defaultActive = this.firstBigId
      this.centerDialogtemplate = true
    },

    // 项目明细
    detailedTemplate() {
      this.$router.replace({
        path: '/Detailedproject',
        query: {
          examUserId: this.examUserId
        }
      })
    },

    // 获取人员基本信息
    getData(sysGuid) {
      let url = '/sys/project/getShowExam'
      this.$post(url, { sysGuid })
        .then(({ data }) => {
          let { user, bigProject, smallProject } = data
          this.ponserData = user
          this.projectList = bigProject.map(item => {
            let t = {
              sysGuid: item.sysGuid,
              pname: item.pname
            }
            t.smallProject = smallProject.filter(items => items.parentId === item.sysGuid)
            return t
          })
          Object.keys(this.reviewForm).forEach(key => {
            this.reviewForm[key] = data.user[key]
          })
        })
        .catch(error => {
          this.$message.error(error)
        })
    },

    // 获取模板数据
    getTemplate(examUserId) {
      this.$post('/dev/examUserProjectItem/getTemplate', {
        examUserId
      }).then(({ data }) => {
        let resultList = data.resultList
          .map(item => {
            item.parentId = data.smallList.find(t => t.projectId === item.projectId).parentId
            return item
          })
          .sort((t, next) => t.projectId - next.projectId)
        let bigList = data.bigList.map(item => {
          item.resultList = resultList.filter(t => t.parentId === item.projectId)
          return item
        })
        this.templateData = data.deptList.map(item => {
          item.bigList = bigList.filter(t => t.deptId === item.sysGuid)
          return item
        })

        // 侧边栏默认展开第一个
        this.firstBigId = this.templateData[0].bigList[0].projectId
        this.defaultActive = this.firstBigId
      })
    }
  },

  computed: {
    ...mapGetters(['o_userinfo']),

    // 处理既往史
    historyDisease() {
      if (!Object.keys(this.ponserData).length) return ''
      let aTemp = []
      ;[
        'historyIllnessJson',
        'historyOperationJson',
        'historyTransfusionJson',
        'historyTraumaJson'
      ].forEach(item => {
        try {
          let arr = JSON.parse(this.ponserData[item])
          aTemp = aTemp.concat(arr.map(t => t.name))
        } catch (err) {}
      })
      return aTemp.join('/')
    }
  },

  filters: {
    getBoxType: function(val) {
      let { nameList } = calcType(val)
      return nameList ? nameList.join('/') : '未分配'
    }
  }
}
</script>
<style lang="less" scoped>
@import '../../styles/FirstTrial/fristtrial.less';
.conclusionsedit {
  .headertitle {
    align-items: center;
    justify-content: space-between;
    .rightbtn {
      display: flex;
      .marginright50 {
        margin-right: 50px;
      }
      div {
        display: flex;
        align-items: center;
        font-size: 14px;
        color: #666666;
        cursor: pointer;
        span {
          width: 25px;
          height: 60px;
        }
      }
    }
  }
  .tabbtn {
    display: flex;
    margin: 10px 20px;
    width: 320px;
    border: 1px solid #ececee;
    border-radius: 10px;
    .flex-height(40px);
    div {
      position: relative;
      width: 160px;
      height: 100%;
      align-items: center;
      display: flex;
      justify-content: center;
      color: #666666;
      span {
        margin-right: 10px;
        color: #a6a6a6;
      }
    }
    div.active:after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      border-width: 10px 14px;
      border-style: solid;
      border-color: #ffc000 transparent transparent transparent;
      bottom: -16px;
    }
    div.active {
      background-color: #ffc000;
      border-radius: 10px;
      color: #fff;
      &:before {
        content: '';
        position: absolute;
        top: -1px;
        left: -1px;
        right: -1px;
        bottom: -1px;
        background: transparent;
        border: 5px solid #ffc000;
        border-radius: 10px;
      }
      span {
        color: #ffffff;
      }
    }
  }
  .jlmain {
    border: solid 1px #ceeae5;
    padding: 0 26px;
    margin: 30px 20px 10px 20px;
    .jl-list {
      border-bottom: 1px dotted #ececee;
      padding: 20px 0;
      .big-title {
        padding: 5px 0;
        font-size: 18px;
      }
      /deep/ .el-textarea__inner {
        font-size: 14px;
        color: #666666;
        border: none;
        padding: 5px 0;
      }
    }
    .jl-list:last-child {
      border-bottom: none;
    }
  }
  .footer {
    align-items: center;
    .btn {
      cursor: pointer;
      width: 101px;
      height: 32px;
      line-height: 32px;
      font-size: 14px;
      border-radius: 4px;
      text-align: center;
      margin-left: 10px;
    }
    .qxbtn {
      color: #07caa6;
      border: solid 1px #00d0a6;
    }
    .qxbtn:hover {
      background-color: #e3fef9;
      border: solid 1px #00d0a6;
    }
    .bcbtn {
      background-color: #00d0a6;
      color: #fff;
    }
    .bcbtn:hover {
      background-color: #2cdfbb;
    }
    .tbbtn {
      background-color: #ffc000;
      color: #fff;
    }
    .tbbtn:hover {
      background-color: #ffcc32;
    }
  }
  .bohuiyy {
    font-size: 14px;
    color: #111111;
    margin-bottom: 20px;
  }
